.Sidebar {
    width: 60px;
    background-color: #f3f3f3;
    padding: 0.5rem 0; 
    display: -ms-flexbox; 
    display: flex; 
    -ms-flex-direction: column; 
        flex-direction: column; 
    -ms-flex-align: center; 
        align-items: center; 
}

.Sidebar .logo {
   font-family: "Fauna One";
   color: #666;
   font-size: 3rem;
}

.Sidebar button {
    background-color: transparent;
    border: 0;
    color: #008BF8;
    cursor: pointer;
    outline: none;
}


