.CanvasList {
    border-left: 1px solid #eee;
    border-right: 1px solid #eee;
    width: 30rem;
}

@media (max-width: 800px) {
    .NoteList {
      width: 20rem;
    }
}

.CanvasList h3 {
    color: #999;
    text-transform: uppercase;
    font-size: 2rem;
    font-family: Oxygen;
    font-weight: 300;
    letter-spacing: 3px;
    margin: 20px 2rem;
}

ul#canvases {
    border-top: 1px solid #eee;
    overflow-y: scroll;
    height: calc(100vh - 72px);
    list-style: none;
    margin-top: 1em;
    padding: 0;
    width: 100%;
    color: #999;
  }


